import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { Signature , StatusTag} from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  
  render() {

    let column = [
      {
        title: getLanguage({id: "transactionDate"}),
        dataIndex: "expected_date",
        rowKey: "expected_date",
        width: "10%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({id: "cashManagements.no"}),
        dataIndex: "cash_flow_no",
        rowKey: "cash_flow_no",
      },
      {
        title: getLanguage({id: "transactionType"}),
        dataIndex: "cash_flow_type",
        rowKey: "cash_flow_type",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "primary";
            display = "Deposit";
          } else {
            className = "active";
            display = "Withdrawal";
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({id: "rate"}),
        dataIndex: "currency_rate",
        rowKey: "currency_rate",
        align:"right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({id: "amount"}),
        dataIndex: "currency_amount",
        rowKey: "currency_amount",
        align:"right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({id: "baseAmount"}),
        dataIndex: "cash_flow_amount",
        rowKey: "cash_flow_amount",
        align:"right",
        render: text => <NumberFormat value={text} />
      },
      // {
      //   title: "Reconciled Date",
      //   dataIndex: "reconciled_date",
      //   rowKey: "reconciled_date",
      //   render: text => {
      //     if (text === null) {
      //       return <span />;
      //     } else {
      //       return <span>{moment(text).format("DD/MM/YYYY")}</span>;
      //     }
      //   }
      // },
    ];

    const data = this.props.data;
    const { bank_reconcile} = data ? data : {};
    const { cash_managements} = data ? data : []; 
    const { loading } = this.props;
    // console.log(bank_reconcile)
    const array = cash_managements ?
    cash_managements.filter((x) => x.reconciled_date === bank_reconcile.reconcile_date)
    : [];

    const leftSide = [
      {
        label: <IntlMessages id="reconcileDate" />,
        description: data 
                      ? moment(bank_reconcile.reconcile_date, "YYYY-MM-DD").format("DD MMMM YYYY")
                      : null
      },
      {
        label: <IntlMessages id="locations.pageHeader" />,
        description: data 
              ?
                bank_reconcile.location 
                  ? bank_reconcile.location.location_name 
                  : null
              : null
      },
      {
        label: <IntlMessages id="cashBanks.pageHeader" />,
        description: data 
          ?
            bank_reconcile.cash_bank 
              ? 
                bank_reconcile.cash_bank.bank_account_number
                  ? bank_reconcile.cash_bank.cash_bank_code + " (" + bank_reconcile.cash_bank.bank_account_number + ")"
                  : bank_reconcile.cash_bank.cash_bank_code
              : null
          : null
      },
      {
        label: <IntlMessages id="currencies.pageHeader" />,
        description: data 
                    ? bank_reconcile.currency 
                      ? bank_reconcile.currency.currency_code 
                      : null
                    : null 
      },
      {
        label: <IntlMessages id="bankStatementBalance" />,
        description: data 
            ?
              bank_reconcile.bank_statement_balance 
              ? <NumberFormat value={bank_reconcile.bank_statement_balance} />
              : null
            : null
      },
      
      
    ];

    const rightSide = [
      {
        label: <IntlMessages id="calculatedAmount" />,
        description: data 
                    ?
                      bank_reconcile.calculated_amount 
                        ? <NumberFormat value={bank_reconcile.calculated_amount} />
                        : null
                    : null
      },
      {
        label: <IntlMessages id="unbalanceAmount" />,
        description: data 
                    ?
                      bank_reconcile.unbalance_amount 
                        ? <NumberFormat value={bank_reconcile.unbalance_amount} />
                        : null
                    : null
      },
    ];

    // const addition = [
    // ];

    // const total = [
    //   {
    //     label: <IntlMessages id="totalCost" />,
    //     amount: this.state.total_cost,
    //     parseAmount: true
    //   },
    //   {
    //     label: <IntlMessages id="totalExpense" />,
    //     amount: data.total_expense,
    //     parseAmount: true
    //   },
    //   {
    //     label: <IntlMessages id="totalAmount" />,
    //     amount: data.total_amount,
    //     parseAmount: true
    //   },
    // ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.bankReconcile" />}
          subtitle={
            <>
              {/* <Row>
                <Col>
                  <strong>{data.cash_flow_no}</strong>
                </Col>
              </Row> */}
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data 
                      ?
                        bank_reconcile.reconcile_date
                          ? moment(
                              bank_reconcile.reconcile_date,
                              "YYYY-MM-DD HH:mm:ss"
                            ).format("DD MMMM YYYY")
                          : null
                      : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="bankReconciles.add.subtitle.data"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={column}
                dataSource={array}
                rowKey={row => row.id_cash_management}
                pagination={false}
              />
            </Col>
          </Row>

          {/* <Row type="flex" style={{ marginTop: "24px" }}>
            <Col span={12}>
              <TotalField data={addition} />
            </Col>
            <Col span={12}>
              <TotalField data={total} />
            </Col>
          </Row> */}

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() => document.title = getFileName("bank_reconcile", null, false)}
          onAfterPrint={() => document.title = siteConfig.documentTitle}
        />
      </>
    );
  }
}

export default Print;
