import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import BankReconcileUpdateForm from "./updateForm";
import BankReconcileDocUpdateForm from "./updateDocForm";
import History from "./history";


import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/bankReconcile/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";
import Print from "./print";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/financials/bank-reconciles";


class UpdateBankReconcile extends Component {

  state = {
    visible: false
  }
  // Get All data diawal
  componentWillMount = () => {
    const { id } = this.props.match.params;
    const { editBankReconcile } = this.props;
    editBankReconcile(id);
    // console.log(this.props.bankReconcile.show_bank_reconcile)
    // if(this.props.bankReconcile.show_bank_reconcile !== null && 
    //   typeof this.props.show_bank_reconcile.bank_reconcile !== "undefined" &&
    //   this.props.show_bank_reconcile.bank_reconcile.id_bank_reconcile === id
    // ){

    // }
    // else{
    //   const { editBankReconcile } = this.props;
    //   editBankReconcile(id);
    // }
    
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.bankReconcile.error) {
      notification("error", this.props.bankReconcile.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.bankReconcile.success) {
      notification("success", this.props.bankReconcile.success);
      this.props.clearSuccess();
    }
  }

  handleShowBankReconcile = (id_cash_bank, start_date, reconcile_date) => {
    const { showBankReconcile } = this.props;
    showBankReconcile(id_cash_bank, start_date, reconcile_date);
  }

  handleSubmit = value => {
    const { updateBankReconcile } = this.props;
    const { id } = this.props.match.params;

    updateBankReconcile(id, value);
  };

  handleModalHistory = () => {
    this.setState({ visible: !this.state.visible })
  };



  render() {
    //console.log(this.props.bankReconcile.show_bank_reconcile);
    const { show_bank_reconcile, loading } = this.props.bankReconcile
    //const show_bank_reconcile = this.props.bankReconcile.show_bank_reconcile ? : []
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="bankReconciles.update.title" />}
            style={{ margin:0 }}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={show_bank_reconcile} />

            <Button 
              style={{ marginLeft: "5px" }} 
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <History
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={
              show_bank_reconcile !== null && show_bank_reconcile.bank_reconcile !== null
                ? show_bank_reconcile.bank_reconcile.description 
                : null
            }
          />

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="bankReconciles.tab.detail" />} 
              key="1" 
            >
              {this.props.bankReconcile.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <BankReconcileUpdateForm
                  submit={this.handleSubmit}
                  bankReconcile={this.props.bankReconcile}
                  permissions={permissions}
                  handleShowBankReconcile={this.handleShowBankReconcile}
                  //processBankReconcile={this.props.processBankReconcile}
                  //handleSubmitCancel={this.handleSubmitCancel}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="bankReconciles.tab.attachment" />} 
              key="2"
            >  
             {this.props.bankReconcile.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <BankReconcileDocUpdateForm
                  submit={this.handleSubmit}
                  bankReconcile={this.props.bankReconcile}
                  downloadDocument={this.props.downloadDocument}
                />    
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    bankReconcile: state.bankReconcile, 
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateBankReconcile);
